module.exports = "<h1 id=getting-started>Getting Started</h1> <h2 id=webpack>Webpack</h2> <p>If you are using module bundlers such as Webpack, Rollup, Laravel elixir/mix, etc you may prefer directly include package into your project. To get started use yarn or npm to get latest version first:</p> <pre class=hljs>yarn add bootstrap-vue <span class=hljs-comment># or npm i bootstrap-vue</span>\n</pre> <p>Then, register BootstrapVue plugin in your app entry point:</p> <pre class=hljs><span class=hljs-keyword>import</span> Vue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;vue&apos;</span>\n<span class=hljs-keyword>import</span> BootstrapVue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue&apos;</span>\n\nVue.use(BootstrapVue);\n</pre> <p>And import styles: (needs <code>style-loader</code>)</p> <pre class=hljs><span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap/dist/css/bootstrap.css&apos;</span>\n<span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap-vue/dist/bootstrap-vue.css&apos;</span>\n</pre> <p><strong>Note:</strong> If you are unable or do not want to add style-loader as a dev dependency, you have to manually include both <a href=https://v4-alpha.getbootstrap.com/getting-started/download/ >Bootstrap</a> and <a href=https://unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.css>BootstrapVue</a> CSS files in your bundle or reference them from <code>static/</code> via <code>index.html</code>.</p> <h2 id=nuxt-js>Nuxt.js</h2> <p>You can use official <a href=https://nuxtjs.org>Nuxt.js</a> module to add BootstrapVue support. (<a href=https://github.com/nuxt-community/modules/tree/master/modules/bootstrap-vue>module docs</a>)</p> <ul> <li>Add <code>@nuxtjs/bootstrap-vue</code> dependency using yarn or npm to your project:</li> <li>Add <code>@nuxtjs/bootstrap-vue</code> to modules section of <strong>nuxt.config.js</strong></li> </ul> <pre class=hljs>{\n  <span class=hljs-attr>modules</span>: [\n    <span class=hljs-string>&apos;@nuxtjs/bootstrap-vue&apos;</span>,\n\n    <span class=hljs-comment>// Or if you have custom bootstrap CSS...</span>\n    [<span class=hljs-string>&apos;@nuxtjs/bootstrap-vue&apos;</span>, { <span class=hljs-attr>css</span>: <span class=hljs-literal>false</span> }],\n  ]\n}\n</pre> <h2 id=vue-cli>vue-cli</h2> <p>Download the dependencies:</p> <pre class=hljs>yarn add --dev bootstrap-vue style-loader\n</pre> <p>In <code>src/main.js</code>, add the following lines, in priority order:</p> <pre class=hljs><span class=hljs-keyword>import</span> Vue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;vue&apos;</span>\n<span class=hljs-keyword>import</span> BootstrapVue <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue&apos;</span>\n<span class=hljs-comment>/* ...there may be other imports here */</span>\n<span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap-vue/dist/bootstrap-vue.css&apos;</span>\n<span class=hljs-keyword>import</span> <span class=hljs-string>&apos;bootstrap/dist/css/bootstrap.css&apos;</span>\n\nVue.use(BootstrapVue)\n</pre> <h2 id=individual-components-and-directives>Individual components and directives</h2> <p>If for any reason just want to use a specific component, you can do this by directly importing that component. This is not recommended as entire package gzipped size is ~40Kb (including Popper.js)!</p> <pre class=hljs><span class=hljs-keyword>import</span> { bAlert, bBtn, bCollapse } <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/lib/components&apos;</span>\n<span class=hljs-keyword>import</span> { bToggle, bScrollspy } <span class=hljs-keyword>from</span> <span class=hljs-string>&apos;bootstrap-vue/lib/directives&apos;</span>\n\n<span class=hljs-keyword>new</span> Vue({\n  <span class=hljs-comment>// ...</span>\n  components: {\n    bAlert,\n    bBtn,\n    bCollapse\n  },\n  <span class=hljs-attr>directives</span>: {\n    bToggle,\n    bScrollspy\n  },\n  <span class=hljs-comment>// ...</span>\n})\n</pre> <h2 id=browser>Browser</h2> <pre class=hljs><span class=hljs-comment>&lt;!-- Add this to &lt;head&gt; --&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>link</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;text/css&quot;</span> <span class=hljs-attr>rel</span>=<span class=hljs-string>&quot;stylesheet&quot;</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap@next/dist/css/bootstrap.min.css&quot;</span>/&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>link</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;text/css&quot;</span> <span class=hljs-attr>rel</span>=<span class=hljs-string>&quot;stylesheet&quot;</span> <span class=hljs-attr>href</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.css&quot;</span>/&gt;</span>\n\n<span class=hljs-comment>&lt;!-- Add this after vue.js --&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span> <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;//unpkg.com/babel-polyfill@latest/dist/polyfill.min.js&quot;</span>&gt;</span><span class=\"undefined\"></span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span> <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;//unpkg.com/bootstrap-vue@latest/dist/bootstrap-vue.js&quot;</span>&gt;</span><span class=\"undefined\"></span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n</pre> <h2 id=build-variants>Build variants</h2> <p>Choosing the best variant for your build environment / packager helps less bundle sizes. If your bundler supports es modules, it will automatically prefer it over commonjs.</p> <table class=\"table b-table table-sm table-striped\"> <thead class=thead-default> <tr> <th>Variant</th> <th>Environments</th> <th>Package path</th> </tr> </thead> <tbody> <tr> <td><strong>ES Module</strong></td> <td>Webpack 2 / Rollup</td> <td><code>dist/bootstrap-vue.esm.js</code></td> </tr> <tr> <td>commonjs2</td> <td>Webpack 1 / ...</td> <td><code>dist/bootstrap-vue.common.js</code></td> </tr> <tr> <td>UMD</td> <td>Browser</td> <td><code>dist/bootstrap-vue.js</code></td> </tr> </tbody> </table> <h2 id=migrating-a-project-already-using-bootstrap>Migrating a project already using Bootstrap</h2> <p>If you&apos;ve already been using Bootstrap 4, there are a couple adjustments you may need to make to your project:</p> <ul> <li>Remove the bootstrap.js file from your page scripts or build pipeline</li> <li>If Bootstrap is the only thing relying on jQuery, you can safely remove it &#x2014; BootstrapVue <strong>does not</strong> depend on jQuery</li> <li>Don&apos;t forget to include the <code>bootstrap-vue.css</code> file!</li> </ul> <h2 id=browsers-support>Browsers Support</h2> <h3 id=css>CSS</h3> <p>BootstrapVue is to be used with Bootstrap 4 CSS. Please see <a href=https://v4-alpha.getbootstrap.com/getting-started/browsers-devices>Browsers and devices</a> for more information about browsers currently supported by Bootstrap 4. </p> <h3 id=js>JS</h3> <p>BootstrapVue is written in Vue! So this is up to your project and bundler that which browsers are supported. If you want to support older IE, Android and IOS devices, you may want to use <a href=https://babeljs.io/docs/usage/polyfill>Babel Polyfill</a></p> <h3 id=ie-11>IE 11</h3> <p>You&apos;ll need babel-polyfill for BootstrapVue to work properly. In order to support this browser: </p> <ul> <li><code>npm install babel-polyfill</code></li> <li>Import it in your app main entry point with <code>import &apos;babel-polyfill&apos;</code></li> </ul> ";

